import RSSParser from 'rss-parser';
import { CORS_PROXY } from 'src/constants';
import { Member, BlogFeed } from 'src/models';
import { MentoringApplyFormDto } from 'src/models/forms';

export async function fetchMemberFeeds(member: Member): Promise<BlogFeed[]> {
  if (!member.rss) {
    return [];
  }

  const parser = new RSSParser();
  const response = await parser.parseURL(`${CORS_PROXY}${member.rss}`);
  if (!response.items) {
    response.items = [];
  }

  const feeds: BlogFeed[] = response.items.map(item => {
    return {
      author: member,
      title: item.title ?? '',
      contentSnippet: item.contentSnippet ?? '',
      pubDate: item.pubDate ?? '',
      link: item.link ?? '',
    };
  });

  return feeds;
}

export async function applyMentoringForm(data: MentoringApplyFormDto): Promise<any> {
  const response = await fetch(
    `https://n6uc80da6i.execute-api.ap-northeast-2.amazonaws.com/prod/mentoring-project/apply`,
    {
      headers: new Headers({
        'Content-Type': 'application/json',
      }),
      method: 'POST',
      body: JSON.stringify(data),
    }
  );
  console.log(await response.json());
}
